.jumbotron{
   background-color: brown;
    width:100%;
    height:300px;
    padding-top: 0;
    padding-left: 0;
}
img{
    width:100%;
    height:300px;
}
.heading{
    padding: 3%;
    color:white;
}
@media screen and (max-width: 640px){
    #headintro{
        font-size:20px;
    }
    #head{
        font-size: 30px;
    }
    .jumbotron{
        height:250px;
    }
    img{
        height:250px;
    }
    .heading{
        padding:1%;
    }
 }
.score{
    color:white;
    background-color: black;
    padding:2%;
}
@media screen and (max-width: 640px){
.score{
    font-size:25px;
}
}
.img-container {
    background: #fff;
    border-radius: 2px;
    height: 120px;
    margin: 1rem;
    position: relative;
    width: 15%;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    float: left;
  }
.imgclass{
    width:100%;
    height:120px;
}
.main{
  margin-left: 5%;
}
@media screen and (max-width: 640px){
  .img-container{
    width:40%;
    height:100px;
  }
  .main{
    margin-left: 1%;
  }
}

